:root {
  --themecolor: #1dd1a1;
}
img {
  width: 100%;
}
.navbar-light .navbar-nav .nav-link {
  color: black;
}
.home-bnr {
  height: 90vh;
  background: linear-gradient(
      185deg,
      rgb(0 0 0 / 0.5) 80%,
      rgb(0 0 0 / 0.5) 80%
    ),
    url(./images/banner1.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}
.headlines_title {
  color: var(--themecolor);
  font-weight: 500;
  font-size: 2rem;
  margin-top: 2rem;
  margin-bottom: 1rem;
}
.hd-col {
  margin-bottom: 2rem;
}
.hd-col:first-child {
  flex-basis: 50%;
  max-width: 50%;
}
.hd-card {
  height: 100%;
}
.hd-card .card-body {
  background-color: #fbfbfb;
}
.hd-card img {
  width: 100%;
  height: 10rem;
  object-fit: cover;
}
.hd-col:first-child img {
  height: 15rem;
}

@media (max-width: 576px) {
  .hd-col:first-child {
    flex-basis: 100%;
    max-width: 100%;
  }
}
